<template>
  <div>
    <el-card>
      <div slot="header" class="clearfix">
        <span>轮播列表</span>
      </div>
      <div>
        <el-form :inline="true">
          <el-form-item label="标题">
            <el-input v-model="queryParams.name" placeholder="标题"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="getBanners()">查询</el-button>
          </el-form-item>
          <el-form-item v-if="addPermission">
            <el-button type="primary" @click="showAddForm = true;editParams={};">新增</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div style="padding:20px;">
        <el-table :data="tableData" style="margin-bottom:20px;">
          <el-table-column prop="groupId" label="groupId"></el-table-column>
          <el-table-column prop="name" label="图片">
            <template slot-scope="scope">
              <el-image style="height: 60px" :src="scope.row.img" fit="scale-down"></el-image>
            </template>
          </el-table-column>
          <el-table-column prop="sort" label="顺序"></el-table-column>
          <el-table-column prop="title" label="标题"></el-table-column>
          <el-table-column prop="alias" label="副标题"></el-table-column>
          <el-table-column prop="createTime" label="创建时间"></el-table-column>
          <el-table-column label="操作" width="120">
            <template slot-scope="scope">
              <el-button v-if="editPermission" type="text" @click="edit(scope.row)">编辑</el-button>
              <el-button v-if="delPermission" type="text" @click="del(scope.row)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination
          :background="false"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="queryParams.pageNum"
          :page-sizes="[10, 20, 30, 40, 50]"
          :page-size="queryParams.pageSize"
          :layout="optionsArr.join(',')"
          :total="total"
        ></el-pagination>
      </div>
    </el-card>
    <el-dialog title="编辑轮播图" width="500px" :visible.sync="showAddForm">
      <el-form
        :model="editParams"
        label-position="right"
        ref="modelForm"
        label-width="100px"
        class="vertical-form"
      >
        <el-form-item label="标题">
          <el-input v-model="editParams.title" placeholder="标题"></el-input>
        </el-form-item>
        <el-form-item label="分组">
          <el-select v-model="editParams.groupId" filterable placeholder="请选择">
            <el-option label="新增分组" :value="0"></el-option>
            <el-option
              v-for="item in groups"
              :key="item.groupId"
              :label="'分组'+item.groupId"
              :value="item.groupId"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="副标题">
          <el-input v-model="editParams.alias" placeholder="副标题"></el-input>
        </el-form-item>
        <el-form-item label="链接">
          <el-input v-model="editParams.link" placeholder="链接"></el-input>
        </el-form-item>
        <el-form-item label="描述">
          <el-input v-model="editParams.description" type="textarea" placeholder="描述"></el-input>
        </el-form-item>
        <el-form-item label="顺序">
          <el-input v-model="editParams.sort" placeholder="顺序"></el-input>
        </el-form-item>
        <el-form-item label="缩略图">
          <el-upload
            action="/api/admin/upload/image"
            :on-remove="handleRemove"
            :on-success="onSuccess"
            :file-list="fileList"
            :limit="1"
            list-type="picture"
          >
            <el-button size="small" type="primary">点击上传</el-button>
            <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
          </el-upload>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="showAddForm = false;reset()">取消</el-button>
        <el-button type="primary" @click="save">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import bannerApi from "@/api/bannerApi";
import store from "@/store";
export default {
  data() {
    return {
      addPermission: false,
      editPermission: false,
      delPermission: false,
      showAddForm: false,
      optionsArr: ["total", "sizes", "prev", "pager", "next", "jumper"],
      fileList: [],
      tableData: [],
      groups: [],
      editParams: { groupId: 1, sort: 1 },
      queryParams: {
        pageNum: 1,
        pageSize: 10
      },
      total: 0
    };
  },
  created() {
    this.addPermission = store.state.user.permissions.indexOf(200103001) > -1;
    this.editPermission = store.state.user.permissions.indexOf(200103002) > -1;
    this.delPermission = store.state.user.permissions.indexOf(200103003) > -1;
    this.getBanners();
    this.group();
  },
  computed: {
    editTypeChange() {
      return this.editType;
    }
  },
  watch: {},
  methods: {
    getBanners() {
      if (this.queryParams.name == "") {
        this.queryParams.name = null;
      }
      bannerApi.page(this.queryParams).then(res => {
        this.tableData = res.data.records;
        this.total = res.data.total;
      });
    },
    group() {
      bannerApi.group().then(res => {
        this.groups = res.data;
        if (this.groups.length == 0) {
          this.editParams.groupId = 0;
        }
      });
    },
    handleSizeChange(val) {
      this.queryParams.pageSize = val;
      this.getBanners();
    },
    handleCurrentChange(val) {
      this.queryParams.pageNum = val;
      this.getBanners();
    },
    onSuccess(res, file, fileList) {
      file.url = res.data.url;
      this.fileList = fileList;
    },
    handleRemove(file, fileList) {
      this.fileList = fileList;
    },
    save(formName) {
      let id = this.editParams.id;
      if (this.fileList.length == 0) {
        this.$message.warning("请上传图片");
        return false;
      }
      this.editParams.img = this.fileList[0].url;
      if (id) {
        bannerApi.update(this.editParams).then(res => {
          this.reset();
          this.$message.success("保存成功");
        });
      } else {
        bannerApi.create(this.editParams).then(res => {
          this.reset();
          this.$message.success("保存成功");
          this.getBanners();
        });
      }
    },
    edit(row) {
      this.editParams = row;
      this.fileList = [{ name: row.img.split("@@")[1], url: row.img }];
      this.showAddForm = true;
    },
    del(row) {
      bannerApi.delete(row.id).then(res => {
        this.$confirm("此操作将永久删除该数据, 是否继续?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            this.$message.success("已删除");
            this.getBanners();
          })
          .catch(() => {});
      });
    },
    reset() {
      this.showAddForm = false;
      this.editParams = { groupId: 1, sort: 1 };
      this.fileList = [];
    }
  }
};
</script>

